
body{
	background: #fff;
}
.register-wrap{
	width:90%;
	margin:0 auto;
}
.logo{
	display: block;
    width: 2.3rem;
    margin: .8rem auto;
}
.qy-btn{
	background: #f46094;
	color:#fff;
	border-radius:1rem;
	font-size: .5rem;
}
.qy-btn:active{
	background:#f45555;
}
.protocol{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	margin:.5rem;
}
.protocol .yd-checkbox{
   	padding:0;
}
.protocol .yd-checkbox-text{
	color:#adb3b9;
	font-size:.3rem;
}
.protocol a{
	color:#81bbf1;
	font-size: .3rem;
	padding-top: .04rem;
}



